@import "reset.scss";
@import "variables.scss";
@import "fonts.scss";

body.lock{
    overflow: hidden;
}

.container {
    max-width: 1200px;
    padding: 0 15px;
    margin: 0 auto;
}

.head-section {
    margin-top: 100px;
    background-color: $ColorWhite;
    @media (max-width: 985px) {
        margin-top: 50px;            
    }
    .head {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: 0 -15px;
        &-main-title {
            display: block;
            flex: 0 1 auto;
            margin: 0 15px;
            font-family: 'Open-Sans', Arial, sans-serif;
            font-size: 36px;
            color: $ColorBlack;
            text-align: left;
            @media (max-width: 985px) {
                text-align: center;
                flex-basis: 100%;
                margin-bottom: 50px;            
            }
        }
        &-input {
            display: flex;
            justify-content: space-between;
            flex: 0 1 75%;
            min-width: 400px;
            @media (max-width: 985px) {
                flex-basis: 100%;
            }
            @media (max-width: 700px) {
                flex-wrap: wrap;
                min-width: 250px;
            }
            #toDo {
                flex: 0 1 80%;
                margin: 0 15px;
                padding: 0 15px;
                font-family: 'Open-Sans', Arial, sans-serif;
                font-weight: 700;
                resize: none;
                overflow: auto;
                -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
                -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
                box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
                @media (max-width: 700px) {
                    flex-basis: 100%;
                    margin-bottom: 25px;
                    padding: 10px 15px;               
                }
            }
            #date {
                flex:0 1 20%;
                margin: 0 15px;
                text-align: center;
                color: gray;
                font-family: 'Open-Sans', Arial, sans-serif;
                font-weight: 700;
                resize: none;
                overflow: auto;
                cursor: pointer;
                -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
                -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
                box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
                @media (max-width: 700px) {
                    flex-basis: 35%;               
                }
            }
            .add-button {
                margin: 0 15px;
                padding: 5px 40px;
                font-family: 'Open-Sans', Arial, sans-serif;
                font-size: 18px;
                color: $ColorWhite;
                background-color: $ColorOrange;
                border-radius: 5px;
                transition: all 0.5s ease 0s;
                &:hover {
                    background: $ColorBlack;
                }
            }
        }
        &-date {
            display: block;
            width: 100%;
            margin: 0 15px 50px;
            font-family: 'Open-Sans', Arial, sans-serif;
            font-size: 36px;
            color: $ColorBlack;
            text-align: right;            
        }
    }    
}

.work-section {
    margin: 160px 0px 100px;
    background-color: $ColorWhite;
    @media (max-width: 700px) {
        margin: 80px 0px 50px;
    }
    .progress-cards {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -15px;
        justify-content: space-between;
        align-items: flex-start;
        @media (max-width: 1140px) {
            justify-content: center;
        }
        .progress-card {
            margin: 0 15px;
            padding: 30px 25px 20px;
            min-width: 350px;
            min-height: 120px;
            flex:0 1 25%;
            -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
            -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
            box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
            font-family: 'Open-Sans', Arial, sans-serif;
            font-weight: 700;
            text-align: left;
            @media (max-width: 1140px) {
                flex-basis: 80%;
                min-width: 250px;
                margin-bottom: 40px;
            }
            @media (max-width: 800px) {
                flex-basis: 100%;
            }
            &-title {
                display: block;
                margin-bottom: 25px;
                font-size: 24px;
                font-weight: 800;
            }
            .in-progress-title {
                color: $ColorOrange;
            }
            .done-title {
                color: $ColorBlue;
            }
            .card-list {
                .item {
                    color: $ColorWhite;
                    padding: 10px;
                    margin-bottom: 25px;
                    border-radius: 15px;
                    font-size: 16px;
                    line-height: 20px;
                    .date {
                        text-align: end;
                        margin-bottom: 15px;
                    }
                    &-text {
                        margin-bottom: 15px;
                    }
                    .buttons {
                        text-align: end;
                        margin: 0px -10px;
                        .button{
                            display: inline-block;
                            background: $ColorWhite;
                            margin: 0px 10px;
                            cursor: pointer;
                            width: 30px;
                            height: 30px;
                            text-align: end;
                            mask-size: 30px 30px;
                            mask-repeat:no-repeat;
                            transition: all 0.5s ease 0s;
                            &:hover {
                                background: $ColorBlack                            
                            }
                        }
                        .edit-button {
                            mask-image: url('../img/edit.svg');                    
                        }
                        .accept-button {
                            mask-image: url('../img/accept.svg');                    
                        }
                        .delete-button {
                            mask-image: url('../img/delete.svg');                    
                        }
                    }
                }
                .item-edit {
                    background-color: $ColorGreen;
                    cursor: all-scroll;
                }
                .item-accept {
                    background-color: $ColorOrange;
                    cursor: all-scroll;
                }
                .item-delete {
                    background-color: $ColorBlue;
                }               
            }
            .list-in-progress {
                .item-in-progress {
                    color: $ColorWhite;
                    background-color: $ColorOrange;
                }
            }            
            .list-done {
                .item-done {
                    color: $ColorWhite;
                    background-color: $ColorBlue;                    
                }
            }
        }
    }
}

.modal {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.75);
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    overflow-x: hidden;
    overflow-y: auto;
    transition: all 0.5s ease 0s;
    &-wrapper {
        min-height: 100vh;
        padding: 30px 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        .modal-content {
            background-color: $ColorWhite;
            padding: 30px;
            font-family: 'Open-Sans', Arial, sans-serif;
            border-radius: 3%;
            .modal-header {
                text-align: end;
                margin-bottom: 15px;
                .close-button {
                    display: inline-block;
                    background: $ColorRed;
                    cursor: pointer;
                    width: 30px;
                    height: 30px;
                    text-align: end;
                    mask-size: 30px 30px;
                    mask-repeat:no-repeat;
                    mask-image: url('../img/close.svg');                    
                    transition: all 0.5s ease 0s;
                        &:hover {
                             background: $ColorBlack                            
                        }
                }                      
            }
            .modal-body {
                margin-bottom: 25px;
                &-text {
                    text-align: center;
                    color: gray;
                    font-size: 18px;
                    font-weight: 700;
                }
                #date-modal {
                    width: 100%;
                    margin-bottom: 25px;
                    text-align: center;
                    color: gray;
                    font-size: 18px;
                    font-weight: 700;
                    resize: none;
                    overflow: auto;
                    cursor: pointer;
                    -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
                    -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
                    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
                }
                #toDo-modal {
                    font-size: 24px;
                    color: gray;
                    font-weight: 500;
                    width: 100%;
                    min-height: 300px;
                    resize: none;
                    overflow: auto;
                    -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
                    -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
                    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
                }

            }
            .modal-footer {
                display: flex;
                justify-content: end;
                .modal-button {
                    margin-left: 25px;
                    padding: 10px 30px;
                    font-size: 18px;
                    cursor: pointer;
                    border-radius: 5px;
                    color: $ColorWhite;
                    transition: all 0.5s ease 0s;
                        &:hover {
                             background: $ColorBlack                            
                        }
                }
                .save-button {
                    background-color: $ColorGreen;
                }
                .cancel-button, .ok-button {
                    background-color: $ColorRed;
                }
            }
        }
    }
}

.modal.open {
    opacity: 1;
    visibility: visible;
}

.drag-hide {
    visibility: hidden;
}

.drag-hover {
    background-color: #FADFAD;
}